@font-face {
	font-family: 'Roboto-Regular';
	src: url('../fonts/Roboto-Regular.eot');
	src: url('../fonts/Roboto-Regular.eot?#iefix') format('embedded-opentype'),
			url('../fonts/Roboto-Regular.woff2') format('woff2'),
			url('../fonts/Roboto-Regular.woff') format('woff'),
			url('../fonts/Roboto-Regular.ttf') format('truetype'),
			url('../fonts/Roboto-Regular.svg#Roboto-Regular') format('svg');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'Roboto-Medium';
	src: url('../fonts/Roboto-Medium.eot');
	src: url('../fonts/Roboto-Medium.eot?#iefix') format('embedded-opentype'),
			url('../fonts/Roboto-Medium.woff2') format('woff2'),
			url('../fonts/Roboto-Medium.woff') format('woff'),
			url('../fonts/Roboto-Medium.ttf') format('truetype'),
			url('../fonts/Roboto-Medium.svg#Roboto-Medium') format('svg');
	font-weight: 500;
	font-style: normal;
}

html, body, body div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, abbr, address, cite, code, del, dfn, em, img, ins, kbd, q, samp, small, strong, sub, sup, var, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, figure, footer, header, menu, nav, section, time, mark, audio, video, details, summary {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font-weight: normal;
	vertical-align: baseline;
	background: transparent;
}

article, aside, figure, footer, header, nav, section, details, summary {display: block;}

html {
	box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: border-box;
  outline: none;
}

img,
object,
embed {max-width: 100%;}

html {overflow-y: scroll;}

ul {list-style: none;}

blockquote, q {quotes: none;}

blockquote:before,
blockquote:after,
q:before,
q:after {content: ''; content: none;}

a {margin: 0; padding: 0; font-size: 100%; vertical-align: baseline; background: transparent;}

del {text-decoration: line-through;}

abbr[title], dfn[title] {border-bottom: 1px dotted #000; cursor: help;}

table {border-collapse: collapse; border-spacing: 0;}
th {font-weight: bold; vertical-align: bottom;}
td {font-weight: normal; vertical-align: top;}

hr {display: block; height: 1px; border: 0; border-top: 1px solid #ccc; margin: 1em 0; padding: 0;}

input, select {vertical-align: middle;}

pre {
    white-space: pre;
    white-space: pre-wrap;
    white-space: pre-line;
    word-wrap: break-word;
}

input[type="radio"] {vertical-align: text-bottom;}
input[type="checkbox"] {vertical-align: bottom;}

select, input, textarea {font: 99% sans-serif;}

table {font-size: inherit; font: 100%;}

small {font-size: 85%;}

strong {font-weight: bold;}

td, td img {vertical-align: top;}

sub, sup {font-size: 75%; line-height: 0; position: relative;}
sup {top: -0.5em;}
sub {bottom: -0.25em;}

pre, code, kbd, samp {font-family: monospace, sans-serif;}

label,
input[type=button],
input[type=submit],
input[type=file],
button {cursor: pointer;}

button, input, select, textarea {margin: 0;}

button,
/*input[type=button] {width: auto; overflow: visible;}*/

body {
	background: #5d5d5d8c;
}

.active {
	border: 4px solid gold !important;
}

.container {
	max-width: 1400px;
	margin: 0 auto;
}

.header {
	box-shadow: 0 0 25px;
	padding: 12px 20px 12px 30px;
	position: relative;
	background: #000;
	.container {
		display: flex;
		justify-content: center;
		align-items: center;
		width: unset;
	}
	a {
		margin-right: 40px;
	}
	&-item {
		display: flex;
		align-items: center;
		&-logo {
			img {
				width: 100px;
			}
		}
	}
	&-title {
		font-family: 'Roboto-Medium';
		font-size: 20px;
		color: gray;
		width: 100%;
		text-align: center;
	}
}

.work-place {
  display: flex;
  height: 88vh;
	align-items: center;
}

.drawing-area {
	margin-top: 37px;
  height: 88vh;
  display: flex;
  align-items: center;
  width: 70%;
	justify-content: center;
	overflow: hidden;
	border: 1px solid gray;
}

.tools {
	&-container {
		width: 115px;
		display: flex;
		flex-wrap: wrap;
		&__tool {
			display: flex;
			justify-content: center;
			border: 4px solid;
			margin: 1px;
			width: 55px;
			height: 55px;
			background: #4c4848;
			img {
				width: 55%;
				filter: invert(100%);
			}
		}
	}
}

.drawing-area canvas {
	background-repeat: repeat;
	background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACAAAAAgCAMAAABEpIrGAAAABlBMVEVMTExVVVUnhsEkAAAAHUlEQVR4AWOAAUYoQOePEAUj3v9oYDQ9gMBoegAAJFwCAbLaTIMAAAAASUVORK5CYII=');
}

.canvas {
	background-repeat: repeat;
	background-size: cover;
}

.tile-view {
	position: relative;
	width: 96px;
	height: 96px;
}

.preview-list {
	align-self: flex-start;
	margin: 20px;
	&-scroller {
		overflow-y: hidden;
		overflow-x: hidden;
		height: 100%;
	}

	&-tile {
		background-repeat: repeat;
		background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACAAAAAgCAMAAABEpIrGAAAABlBMVEVMTExVVVUnhsEkAAAAHUlEQVR4AWOAAUYoQOePEAUj3v9oYDQ9gMBoegAAJFwCAbLaTIMAAAAASUVORK5CYII=');
		position: relative;
		border: 4px solid;
		margin-bottom: 5px;
	}
	&-duplicate {
		display: none;
		position: absolute;
		right: 0;
		bottom: 0;
		width: 25px;
		height: 25px;
		border: none;
		background: #4c4848;
		border-left: 2px solid;
		border-top: 2px solid;
		img {
			filter: invert(100%);
		}
	}
	&-delete {
		display: none;
		position: absolute;
		width: 25px;
		height: 25px;
		border: none;
		right: 0;
		background: #4c4848;
		border-left: 2px solid;
		border-bottom: 2px solid;
		img {
			filter: invert(100%);
		}
	}
	&-move {
		display: none;
		position: absolute;
		width: 25px;
		height: 25px;
		border: none;
		left: 0;
		bottom: 0;
		background: #4c4848;
		border-right: 2px solid;
		border-top: 2px solid;
		img {
			filter: invert(100%);
		}
	}
	&-sortable {
		max-height: 80vh;
		overflow: auto;	
	}
}

.preview-list-tile:hover .preview-list-duplicate { 
	display: block;
}

.preview-list-tile:hover .preview-list-delete { 
	display: block;
}

.preview-list-tile:hover .preview-list-move { 
	display: block;
}

#new_frame {
	background: #4c4848;
	color: #fff;
  width: 100%;
  line-height: 30px;
  border: 1px solid;
}

.animated {
	align-self: flex-start;
	margin: 20px;
	&-preview {
		background-repeat: repeat;
		background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACAAAAAgCAMAAABEpIrGAAAABlBMVEVMTExVVVUnhsEkAAAAHUlEQVR4AWOAAUYoQOePEAUj3v9oYDQ9gMBoegAAJFwCAbLaTIMAAAAASUVORK5CYII=');
		height: 200px;
    width: 200px;
		&__fps {
			background: gray;
			display: flex;
			padding: 5px 0;
			justify-content: center;
			label, span {
				margin-right: 5px;
				color: #d8d8d8;
				letter-spacing: 1.1px;
			}
		}
	}
}





